import React, { useState, useRef, useEffect } from 'react';

interface EditableElementProps {
  id: string;
  text?: string; // D√ºzenlenecek metin
  transform?: { x: number; y: number }; // Konum verisi
  isInteractive?: boolean; // Sadece edit√∂rde mi √ßalƒ±≈üsƒ±n?
  onUpdate?: (id: string, newText: string | undefined, newTransform: { x: number; y: number }) => void;
  className?: string;
  tagName?: any; // h1, p, div, button vb.
  scale?: number; // Canvas scale oranƒ± (mouse hareketi i√ßin)
  children?: React.ReactNode;
}

export const EditableElement = ({
  id,
  text,
  transform = { x: 0, y: 0 },
  isInteractive = false,
  onUpdate,
  className = '',
  tagName: Tag = 'div',
  scale = 1,
  children
}: EditableElementProps) => {
  const [isDragging, setIsDragging] = useState(false);
  const [pos, setPos] = useState(transform);
  const dragStart = useRef({ x: 0, y: 0 });

  // Dƒ±≈üarƒ±dan gelen transform deƒüi≈üirse g√ºncelle
  useEffect(() => {
    setPos(transform);
  }, [transform.x, transform.y]);

  const handleMouseDown = (e: React.MouseEvent) => {
    if (!isInteractive) return;
    e.stopPropagation(); // Parent eventleri engelle
    setIsDragging(true);
    dragStart.current = { x: e.clientX - pos.x * scale, y: e.clientY - pos.y * scale };
  };

  const handleMouseMove = (e: MouseEvent) => {
    if (!isDragging) return;
    const newX = (e.clientX - dragStart.current.x) / scale;
    const newY = (e.clientY - dragStart.current.y) / scale;
    setPos({ x: newX, y: newY });
  };

  const handleMouseUp = () => {
    if (!isDragging) return;
    setIsDragging(false);
    if (onUpdate) {
      onUpdate(id, text, pos);
    }
  };

  // Global mouse eventleri
  useEffect(() => {
    if (isDragging) {
      window.addEventListener('mousemove', handleMouseMove);
      window.addEventListener('mouseup', handleMouseUp);
    } else {
      window.removeEventListener('mousemove', handleMouseMove);
      window.removeEventListener('mouseup', handleMouseUp);
    }
    return () => {
      window.removeEventListener('mousemove', handleMouseMove);
      window.removeEventListener('mouseup', handleMouseUp);
    };
  }, [isDragging]);

  // √áift tƒ±klama ile metin d√ºzenleme
  const handleDoubleClick = (e: React.MouseEvent) => {
    if (!isInteractive || !text || !onUpdate) return;
    e.stopPropagation();
    const newText = window.prompt("Metni D√ºzenle:", text);
    if (newText !== null) {
      onUpdate(id, newText, pos);
    }
  };

  return (
    <Tag
      className={`${className} ${isInteractive ? 'cursor-move hover:outline hover:outline-2 hover:outline-blue-400 hover:outline-dashed relative z-50' : ''}`}
      style={{
        transform: `translate(${pos.x}px, ${pos.y}px)`,
        transition: isDragging ? 'none' : 'transform 0.2s',
        userSelect: 'none'
      }}
      onMouseDown={handleMouseDown}
      onDoubleClick={handleDoubleClick}
    >
      {children || text}
    </Tag>
  );
};
