// src/components/renderer/layouts/fourpage.tsx
import { SlideData, Language } from '@/core/types';
import { PhoneMockup } from '../PhoneMockup';
import { Gamepad2, Trophy, Target, Medal, Star, Zap, Sparkles } from 'lucide-react';
import { EditableElement } from '../EditableElement';

export const LayoutGame = ({ data, lang, isInteractive, onUpdate }: any) => {
  const content = data.content[lang] || data.content['en'] || Object.values(data.content)[0];
  if (!content) return null;
  
  const transforms = (data as any).elementTransforms || {};

  const t_mission = (content as any).card_mission || "G√ñREV";
  const t_action = (content as any).card_action || "BUL & √áEK";
  const t_win = (content as any).card_win || "KAZAN";
  const t_reward = (content as any).card_reward || "PUANLAR";
  const t_new = (content as any).card_new || "YENƒ∞!";

  return (
    <div className="w-full h-full relative overflow-hidden flex flex-col bg-[#FFF8E1] text-slate-900">
      
      {/* --- ARKA PLAN (2. ve 3. sayfa stili) --- */}
      {/* Desen */}
      <div className="absolute inset-0 opacity-10" 
           style={{ backgroundImage: 'radial-gradient(#FF8F00 3px, transparent 3px)', backgroundSize: '30px 30px' }} />
      
      {/* Blob'lar */}
      <div className="absolute -top-32 -left-32 w-[600px] h-[600px] bg-orange-200 rounded-full blur-3xl opacity-60" />
      <div className="absolute top-1/2 -right-40 w-[500px] h-[500px] bg-yellow-200 rounded-full blur-3xl opacity-60" />
      <div className="absolute -bottom-20 left-20 w-[400px] h-[400px] bg-red-100 rounded-full blur-3xl opacity-60" />

      {/* --- BA≈ûLIK ALANI --- */}
      <div className="z-20 pt-16 px-8 text-center flex flex-col items-center relative">
        
        {/* Badge (2. sayfadaki gibi) */}
        <EditableElement id="subtitle" text={content.subtitle} transform={transforms.subtitle} isInteractive={isInteractive} onUpdate={onUpdate} scale={0.65} className="z-30">
         <div className="inline-flex items-center gap-3 bg-white border-4 border-orange-400 px-8 py-3 rounded-full shadow-[0_8px_20px_rgba(255,167,38,0.3)] mb-6 transform -rotate-2 hover:scale-105 transition-transform">
            <Gamepad2 className="text-orange-500" size={32} />
            <span className="text-2xl font-black text-orange-500 uppercase tracking-widest font-[var(--font-fredoka)]">
                {content.subtitle}
            </span>
         </div>
        </EditableElement>
        
        {/* B√ºy√ºk Ba≈ülƒ±k */}
        <EditableElement id="title" text={content.title} transform={transforms.title} isInteractive={isInteractive} onUpdate={onUpdate} scale={0.65} tagName="h1" className="text-[90px] font-[var(--font-luckiest)] text-slate-800 leading-none drop-shadow-2xl mb-2 stroke-white relative z-30">
            {/* Ba≈ülƒ±k S√ºsleri */}
            <Star className="absolute -top-8 -right-12 text-yellow-400 fill-yellow-400 w-16 h-16" />
        </EditableElement>
      </div>

      {/* --- OYUN ALANI (ORTA) --- */}
      <div className="flex-1 flex items-center justify-center relative z-20 -mt-4">
        
        {/* Sol: G√∂rev Kartƒ± */}
        <div className="absolute left-12 top-1/4 z-30">
            <div className="bg-white p-5 rounded-[30px] rounded-br-none border-[5px] border-blue-400 shadow-xl transform -rotate-6 w-56 flex flex-col items-center group hover:scale-110 transition-transform">
                <div className="bg-blue-100 p-4 rounded-full mb-3 group-hover:rotate-12 transition-transform">
                    <Target className="text-blue-500" size={40} />
                </div>
                <EditableElement id="card_mission" text={t_mission} transform={transforms.card_mission} isInteractive={isInteractive} onUpdate={onUpdate} scale={0.65} className="font-[var(--font-fredoka)] font-bold text-slate-500 text-sm uppercase tracking-wider" />
                <EditableElement id="card_action" text={t_action} transform={transforms.card_action} isInteractive={isInteractive} onUpdate={onUpdate} scale={0.65} className="font-[var(--font-luckiest)] text-blue-500 text-3xl" />
            </div>
        </div>

        {/* Orta: Telefon */}
        <div className="relative transform scale-110 z-20 mx-10">
            {/* Arkadaki Glow */}
            <div className="absolute inset-0 bg-orange-400 rounded-full blur-[90px] opacity-30" />
            
            <PhoneMockup phone={data.phone} image={data.image} />

            {/* Efektler */}
            <Sparkles className="absolute top-10 -right-10 text-yellow-400 w-12 h-12" />
            <Zap className="absolute bottom-20 -left-8 text-orange-500 fill-orange-500 w-10 h-10" />
        </div>

        {/* Saƒü: √ñd√ºl Kartƒ± */}
        <div className="absolute right-12 top-1/3 z-30">
             <div className="bg-white p-5 rounded-[30px] rounded-bl-none border-[5px] border-green-400 shadow-xl transform rotate-6 w-56 flex flex-col items-center group hover:scale-110 transition-transform">
                <div className="bg-green-100 p-4 rounded-full mb-3 group-hover:-rotate-12 transition-transform">
                    <Trophy className="text-green-500 fill-green-500" size={40} />
                </div>
                <EditableElement id="card_win" text={t_win} transform={transforms.card_win} isInteractive={isInteractive} onUpdate={onUpdate} scale={0.65} className="font-[var(--font-fredoka)] font-bold text-slate-500 text-sm uppercase tracking-wider" />
                <EditableElement id="card_reward" text={t_reward} transform={transforms.card_reward} isInteractive={isInteractive} onUpdate={onUpdate} scale={0.65} className="font-[var(--font-luckiest)] text-green-500 text-4xl" />
            </div>
        </div>

      </div>

      {/* --- ALT ALAN --- */}
      <div className="z-20 pb-16 px-12 text-center">
         {/* A√ßƒ±klama Kutusu */}
         <EditableElement id="description" text={(content as any).description} transform={transforms.description} isInteractive={isInteractive} onUpdate={onUpdate} scale={0.65}>
            <div className="bg-white/80 backdrop-blur-md border-[6px] border-white rounded-[40px] p-6 shadow-xl max-w-4xl mx-auto transform -rotate-1 hover:rotate-0 transition-transform duration-300">
                <p className="text-2xl font-bold text-slate-600 font-[var(--font-fredoka)] leading-relaxed">
                    {(content as any).description}
                </p>
            </div>
         </EditableElement>
         
         {/* CTA Butonu */}
         <div className="mt-8">
            <EditableElement id="cta" text={content.cta} transform={transforms.cta} isInteractive={isInteractive} onUpdate={onUpdate} scale={0.65} tagName="button" className="group relative inline-flex items-center justify-center gap-3 bg-gradient-to-b from-orange-400 to-orange-600 text-white text-3xl font-black py-5 px-16 rounded-3xl shadow-[0_8px_0_#E65100] hover:translate-y-1 hover:shadow-[0_4px_0_#E65100] transition-all font-[var(--font-luckiest)] uppercase tracking-wide">
                <Medal size={32} />
                {content.cta}
                <div className="absolute -top-2 -right-2 bg-red-500 text-white text-xs font-bold px-3 py-1 rounded-full transform rotate-12 border-2 border-white shadow-sm">
                    {t_new}
                </div>
            </EditableElement>
         </div>
      </div>

    </div>
  );
};